<template>
  <div class="wrapper">
    <!-- <typeChange @change="changeType"></typeChange> -->
    <div class="content">
      <div class="c-left">
        <ctitle :title="'信访量分布'"></ctitle>
        <div class="detail">
          <p class="allNumber">
            <span>
              <incNumber class="number" :incNumber="jctotal"></incNumber> </span
            >件次
          </p>
          <p class="allNumber">
            <span>
              <incNumber
                class="number"
                :incNumber="jctotal1"
              ></incNumber> </span
            >人次
          </p>
          <div class="top3">
            <div class="label">TOP</div>
            <ul class="list">
              <li class="item" v-for="(item, index) of jcTop3" :key="index">
                <span class="order">{{ index + 1 }}</span>
                <span class="name">{{ item.name }}</span>
                <incNumber class="number" :incNumber="item.value"></incNumber>
                <span class="unit">件次</span>
              </li>
            </ul>
          </div>
        </div>
        <!-- <Map class="map" :params="mapParams" @change="handleMapClick"></Map> -->

        <div
          v-if="mapParams.options.JSON !== '440000'"
          class="map-back"
          @click="mapBack"
        ></div>
        <transition name="fade">
          <Map v-if="mapFlag" :params="mapParams" @change="handleMap"></Map>
        </transition>
      </div>
      <div class="c-right">
        <div class="c-r-t">
          <ctitle :title="'信访量走势'"></ctitle>
          <div class="inner">
            <timeChange
              class="timeChange"
              :key="curTypeId + curCity"
              @change="xflzsTimeClick"
            ></timeChange>
            <LinesR :params="xflzsLineParams"></LinesR>
          </div>
        </div>
        <div class="c-r-c">
          <ctitle :title="'信访人群'"></ctitle>
          <div class="c-r-b">
            <!-- <LinesR :params="linesParams"></LinesR> -->
            <div class="inner" style="height:200px;margin-top:0.4rem;">
              <barline class="chart" :params="nlzbBarParams"></barline>
              <p
                class="label"
                style="font-size:20px; margin-top:0.3rem;font-weight:600;"
              >
                年龄分布
              </p>
            </div>
            <div class="inner" style="height:200px;margin-top:0.4rem;">
              <!-- <barline
                class="chart"
                fontSize="4px"
                :params="nlzbBarParams"
              ></barline> -->
              <loopPie :params="tswtPieParams"></loopPie>

              <p
                class="label"
                style="font-size:20px; margin-top:0.3rem; font-weight:600;"
              >
                性别分布
              </p>
            </div>
          </div>
        </div>
        <!--<div class="c-r-b">-->
        <!--<ctitle :title="'信访量万人比走势'"></ctitle>-->
        <!--<div class="inner">-->
        <!--<timeChange class="timeChange" @change="wrbzsTimeClick"></timeChange>-->
        <!--<LinesR :params="wrbzsLineParams"></LinesR>-->
        <!--</div>-->
        <!--</div>-->
      </div>
    </div>
  </div>
</template>

<script>
import { get_http } from "@/api/index";
import ctitle from "@/components/title/index";
import incNumber from "@/components/incNumber/index";
import typeChange from "@/components/typeChange/index";
import timeChange from "@/components/timeChange/index";
import LinesR from "@/components/line/LinesR";
import Map from "@/components/map/Map";
import barline from "@/components/bar/BarLine";
import loopPie from "@/components/pie/LoopPie";

export default {
  name: "lmwsts",
  components: {
    ctitle,
    incNumber,
    typeChange,
    timeChange,
    LinesR,
    Map,
    barline,
    loopPie
  },
  data() {
    return {
      curCity: "广东省",
      shijCity: "",
      shej_02: "invalid",
      shij_02: "invalid",
      xj_02: "invalid",
      nameP: "",
      mapJson: {
        广东省: "440000",
        广州市: "440100",
        韶关市: "440200",
        深圳市: "440300",
        珠海市: "440400",
        汕头市: "440500",
        佛山市: "440600",
        江门市: "440700",
        湛江市: "440800",
        茂名市: "440900",
        肇庆市: "441200",
        惠州市: "441300",
        梅州市: "441400",
        汕尾市: "441500",
        河源市: "441600",
        阳江市: "441700",
        清远市: "441800",
        东莞市: "441900",
        中山市: "442000",
        潮州市: "445100",
        揭阳市: "445200",
        云浮市: "445300"
      },
      // 投诉问题
      tswtPieParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        options: {
          bgSize: 220,
          colors: this.$STANDARD.pieColors,
          radius: ["55%", "75%"],
          center: ["50%", "50%"],
          centerRadius: ["42%", "43%"],
          labelRich: {
            b: this.fontSize(0.15),
            d: this.fontSize(0.18),
            bHeight: this.fontSize(0.2)
          },
          length: 20,
          length2: 50,
          bRichPadding: [30, -50, 0, -50],
          borderWidth: 0,
          borderColor: "#0B2244",
          itemLabelShow: true, //数据扇区label是否显示
          //图例
          legend: {
            show: false
          },
          rich: {
            bfontsize: "10%" //文字大小
          },
          labelSize: "22%", // label字号
          threeLevel: true,
          rollFlag: true
        }
      },
      //3.热点问题分布
      rdwtCloudParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        options: {
          isClick: false
        }
      },
      mapFlag: false,

      // 3年龄统计
      nlzbBarParams: {
        data: [],
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        options: {
          xName: "name", //x轴字段
          axisValueShow: true,
          splitLineShow: false,
          axis1Name: "人次",
          axisLabelSize: 12, // label字号
          left: "10%",
          right: "5%",
          top: "5%",
          bottom: "0%",
          barWidth: 30,
          barLabelOptions: "top",
          barLabelShow: true, //柱状图数量值显示
          colors: {
            bar: [["#d2f69d", "#79cc7a"]]
          },
          map: {
            value: "数量"
          }
        }
      },
      curTypeId: "drop",
      curCity: "drop",
      //1地图左上角(件次总数)
      jctotal: 0,
      jctotal1: 0,
      //地图左上角(top3)
      jcTop3: [],
      //2地图
      // mapParams: {
      //   canvasStyle: {
      //     width: "100%",
      //     height: "100%"
      //   },
      //   data: [],
      //   options: {
      //     JSON: "hebei",
      //     xName: "name",
      //     yName: "value",
      //     map: {
      //       value: "信访量"
      //     },
      //     carousel: false
      //   }
      // },
      mapParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        options: {
          isHome: true,
          JSON: "440000",
          xName: "name",
          yName: "value_g0_1",
          map: {
            value_g0_1: "总数",
            value_g1_1: "来信",
            value_g1_2: "来访",
            value_g1_3: "网上投诉"
          },
          carousel: true
        }
      },
      // 3信访量走势
      xflzsLineParams: {
        data: [],
        colors: [
          // 紫
          {
            line: "rgba(220, 150, 209, 1)",
            start: "rgba(220, 150, 209, 0.3)",
            end: "rgba(220, 150, 209, 0)"
          }
        ],
        option: {
          splitNumber: 4,
          splitLineShow: true,
          legendShow: false,
          legenditemWidth: 22,
          legenditemHeight: 4,
          axisLabelShow: true,
          axisTickShow: false,
          axisCategoryName: "月",
          axisYName: "件次",
          axisLabelSize: 14,
          scales: true,
          axisLabelFontSize: 16,
          yaxisLabelFontSize: 14,
          Tofixed: false,
          smooth: false,
          areaShow: 1,
          smoothMonotone: "none",
          dataZoomshow: false,
          legendIcon: "rect",
          left: "5%",
          right: "10%",
          top: "15%",
          bottom: "10%",
          legendTop: "2%",
          legendRight: "10%"
        },
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        map: {
          value: "数量"
        },
        xName: "name"
      },
      //4信访量同比、环比走势
      linesParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        colors: [
          {
            line: "#ffb435",
            start: "rgba(255, 180, 53, .5)",
            end: "rgba(255, 180, 53, .5)"
          },
          {
            line: "#1a82f7",
            start: "rgba(26, 130, 247, .5)",
            end: "rgba(26, 130, 247, .5)"
          },
          {
            line: "#e11770",
            start: "rgba(225, 23, 112, .5)",
            end: "rgba(225, 23, 112, .5)"
          },
          {
            line: "#a1d871",
            start: "rgba(161, 216, 113, .5)",
            end: "rgba(161, 216, 113, .5)"
          },
          {
            line: "#ff825b",
            start: "rgba(255, 130, 91, .5)",
            end: "rgba(255, 130, 91, .5)"
          }
        ],
        option: {
          splitNumber: 10,
          axisLabelColor: "#fff",
          yaxisLabelColor: "#82d8ff",
          legendfontSize: 18,
          splitLineShow: true,
          legenditemWidth: 22,
          legenditemHeight: 4,
          axisLabelShow: true,
          axisTickShow: false,
          axisYName: "%",
          axisCategoryName: "月",
          axisLabelSize: 14,
          scales: true,
          axisLabelFontSize: 16,
          yaxisLabelFontSize: 14,
          Tofixed: false,
          smooth: true,
          areaShow: 0,
          smoothMonotone: "none",
          dataZoomshow: false,
          left: "5%",
          right: "10%",
          top: "15%",
          bottom: "10%",
          legendTop: "2%",
          legendRight: "10%"
        },
        map: {},
        xName: "name"
      },
      // 5万人比走势
      wrbzsLineParams: {
        data: [],
        colors: [
          // 紫
          {
            line: "rgba(220, 150, 209, 1)",
            start: "rgba(220, 150, 209, 0.3)",
            end: "rgba(220, 150, 209, 0)"
          }
        ],
        option: {
          splitLineShow: true,
          legendShow: false,
          legenditemWidth: 22,
          legenditemHeight: 4,
          axisLabelShow: true,
          axisTickShow: false,
          axisCategoryName: "年",
          axisLabelSize: 14,
          scales: true,
          axisLabelFontSize: 16,
          yaxisLabelFontSize: 14,
          Tofixed: false,
          smooth: false,
          areaShow: 1,
          smoothMonotone: "none",
          dataZoomshow: false,
          legendIcon: "rect",
          left: "5%",
          right: "10%",
          top: "15%",
          bottom: "10%",
          legendTop: "2%",
          legendRight: "15%"
        },
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        map: {
          value: "数量"
        },
        xName: "name"
      }
    };
  },
  mounted() {
    this.getAPI(this.data, this.curTypeId);
    this.getRightAPI();
    this.getMapAPI(this.date);
    this.getAPI(this.year);
  },
  methods: {
    // 4.全省信访量-地图
    getMapAPI(date) {
      this.getRightAPI();

      console.log(this.curCity);

      // 处理接口参数
      // 省级
      if (this.curCity === "广东省") {
        this.shej_02 = this.curCity;
        this.shij_02 = "invalid";
        this.xj_02 = "invalid";
        this.nameP = "shij_02";
      } else {
        // 市级
        if (this.shijCity) {
          this.shej_02 = "广东省";
          this.shij_02 = this.shijCity;
          this.xj_02 = "invalid";
          this.nameP = "xj_02";
        } else {
          // 县区级
          this.shej_02 = "广东省";
          this.shij_02 = this.shijCity;
          this.xj_02 = this.curCity;
          this.nameP = "invalid";
        }
      }
      get_http("xfzlyjZlfxMap", {
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
        name: this.nameP,
        day: `[${date.startDate},${date.endDate}]`
      })
        .then(res => {
          this.mapParams.options.map = res.data.map;
          this.mapParams.data = res.data.data;
          this.mapFlag = true;
          this.getAPI(date);
        })
        .catch(err => {
          console.log(err);
        });
      // 8.投诉问题分布
      get_http("xfzlyjZlfxTswtzbPie")
        .then(res => {
          this.tswtPieParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 点击地图下钻
    handleMap(name) {
      this.curCity = name;
      for (let key in this.mapJson) {
        if (this.curCity === key) {
          this.mapFlag = false;
          this.mapParams.options.JSON = this.mapJson[name];
        }
      }
    },
    fontSize(res) {
      let docEl = document.documentElement,
        clientWidth =
          window.innerWidth ||
          document.documentElement.clientWidth ||
          document.body.clientWidth;
      if (!clientWidth) return;
      let fontSize = 100 * (clientWidth / 1920);
      return res * fontSize;
    },
    // 地图返回全省
    mapBack() {
      this.mapFlag = false;
      this.curCity = "广东省";
      this.mapParams.options.JSON = "440000";
    },
    // 3年龄分布
    getRightAPI(data) {
      get_http("xfzlyjZlfxNlzbBar", { data: data })
        .then(res => {
          this.nlzbBarParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 头部类型切换
    changeType(id) {
      this.curTypeId = id;
      this.getAPI(this.data, this.curTypeId);
    },
    // handleMapClick(name) {
    //   this.curCity = name === "全省" ? "drop" : name;
    //   this.getOtherAPI(this.year, this.curTypeId);
    // },
    getAPI(data, id) {
      this.getOtherAPI(data, id);
      // 1件次总数（地图左上角）件次
      get_http("xfzlyjZlfxFbTotal", { data: data, xfxs: id })
        .then(res => {
          this.jctotal = res.data.data[0].value;
        })
        .catch(err => {
          console.log(err);
        });
      //人次
      get_http("xfzlyjZlfxRCTotal", { data: data, xfxs: id })
        .then(res => {
          this.jctotal1 = res.data.data[0].value;
        })
        .catch(err => {
          console.log(err);
        });
      // 2地图
      get_http("xfzlyjZlfxMaptop", { data: data, xfxs: id })
        .then(res => {
          // this.mapParams.data = res.data.data;
          this.jcTop3 = res.data.data
            .sort((a, b) => {
              return b.value - a.value;
            })
            .slice(0, 3);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getOtherAPI(data, id) {
      // 3信访量走势
      this.getXflzsLineAPI(data, id, "年度");
      // 4信访量同比、环比走势
      get_http("zlfxWtsdThbzsLine", {
        data: 2020,
        shij_02: this.curCity,
        xfxs: id
      })
        .then(res => {
          this.linesParams.map = res.data.map;
          this.linesParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      // 5万人比走势(2020-8-18注释 原因：后台没有数据)
      // this.getWrbzsLineAPI(year, id, '年度');
    },
    // 3信访量走势
    getXflzsLineAPI(data, id, time) {
      let timetype = "",
        _year = "";
      if (time === "年度") {
        timetype = "cy";
        _year = "[now-4, now]";
      } else {
        timetype = "cm";
        _year = data;
      }
      get_http("xfzlyjZlfxZsLine", {
        year: data,
        xfxs: id,
        shij_02: this.curCity,
        timetype: timetype
      })
        .then(res => {
          this.xflzsLineParams.option.axisCategoryName =
            time === "年度" ? "月" : "月";
          this.xflzsLineParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 5万人比走势
    getWrbzsLineAPI(data, id, time) {
      let timetype = "";
      timetype = time === "年度" ? "cy" : "cm";
      get_http("zlfxWtsdWrbzsLine", {
        data: data,
        xfxs: id,
        timetype: timetype
      })
        .then(res => {
          this.wrbzsLineParams.option.axisCategoryName =
            time === "年度" ? "年" : "月";
          this.wrbzsLineParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 信访量走势-时间切换
    xflzsTimeClick(time) {
      this.getXflzsLineAPI(this.data, this.curTypeId, time);
    },
    // 万人比走势-时间切换
    wrbzsTimeClick(time) {
      this.getWrbzsLineAPI(this.data, this.curTypeId, time);
    }
  },
  computed: {
    date() {
      return this.$store.state.date;
    }
  },
  watch: {
    date(val) {
      this.getAPI(val, this.curTypeId);
    },
    curCity: function(newVal, oldVal) {
      if (newVal === "广东省") {
        this.getMapAPI(this.date);
      } else {
        let c = "";
        for (let key in this.mapJson) {
          if (newVal === key) {
            this.shijCity = newVal;
            c = newVal;
            this.getMapAPI(this.date);
          }
        }
        if (!c) {
          this.xj_02 = newVal;
          this.getAPI(this.date);
        }
      }
    },
    date(val) {
      this.getMapAPI(val);
    }
  }
};
</script>

<style lang="stylus" scoped>
.label
  font-size 0.3rem
  text-align center
  color #fefefe
.wrapper
  position relative
  top 0.8rem
  height calc(100% - 0.8rem)
  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box
    .inner
      width 100%
      height calc(100% - 0.28rem)
    .c-left
      position relative
      flex 1

      .map-back
        position absolute
        top 30px
        right 60px
        width 30px
        height 30px
        background url("~@/assets/back-icon.png") no-repeat
        z-index 1000
        font-size 0.2rem
        color #fff
        cursor pointer
      .map
        position absolute
        top -40px
        left 100px
      .detail
        position absolute
        top 0.6rem
        left 0.6rem
        z-index 1000

        .allNumber
          font-size 0.2rem
          color #5aceff
          margin-bottom 0.2rem
          .number
            font-size 0.5rem
            color #fff
            text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
            margin-right 0.15rem
        .top3
          .label
            float left
            width 0.5rem
            color #ffa800
            font-size 0.18rem
            font-weight 700
            line-height 0.4rem

          .list
            float left
            .item
              line-height 0.4rem
              font-size 0
              color #fff
              .order
                font-size 0.18rem
                margin-right 0.1rem
              .name
                display inline-block
                width 0.8rem
                font-size 0.18rem
              .number
                font-size 0.18rem
              .unit
                font-size 0.14rem
                color #5aceff
                margin-left 0.1rem
    .c-right
      flex 1
      display flex
      flex-direction column
      width 100%
      height 100%
      .timeChange
        top 0
        right .8rem
      .c-r-t
        flex 1
        position relative
      .c-r-c
        flex 1

    .c-r-b
      flex 1
      position relative
      display flex
      justify-content space-between
        .inner2
          flex 1
          .label2
            font-size 12px
</style>
